






































	import xHeader from "../../components/xHeader.vue"
	export default {
		data() {
			return {
				titleh: '规培考试',
				iconh: '&#xe600;',
				items: [{
						value: 'A',
						name: '好发于中年女性及青少年，免疫学检查可见多种自身抗体',
						checked: false
					},
					{
						value: 'B',
						name: '多见于儿童及青少年，临床主要表现为蛋白尿或NS',
						checked: false
					},
					{
						value: 'C',
						name: '轻症一般使用一个抗生素',
						checked: false
					},
					{
						value: 'D',
						name: '注意调节尿液酸碱度以增强抗菌药物效果',
						checked: false
					}
				],
				current: 'A',
				click_num: 1 
			}
		},
		components: {
            xHeader
        },
		onNavigationBarButtonTap(e) {
			this.tips = e
		},
		onLoad() {

		},
		methods: {
			checked: function(check) {
				let that = this;
				if(that.click_num == 1) {
					that.click_num++
					for(var i=0;i<that.items.length;i++) {
						if(check == that.current) {
							if(that.items[i].value == check ) {
								that.items[i].checked = !that.items[i].checked;
							}
						}else {
							if(that.items[i].value == check || that.items[i].value == that.current) {
								that.items[i].checked = !that.items[i].checked;
							}
						}
					}
				}else{
					return false;
				}
			}
		}
	}
